.sidebar {
  width: 28%;
  background-color: $color-default-white;
  color: $color-default-black;
  padding: 38px 30px 77px;
  display: flex;
  flex-direction: column;

  &__title {
    margin-bottom: 38px;
    display: flex;
    align-items: center;
    font-size: 26px;
    font-weight: 600;
  }

  &__title-v {
    font-size: 10px;
    color: $color-font-link;
    margin-left: 6px;
    margin-top: 8px;
  }

  &__logo {
    display: inline-block;
    margin-right: 6px;
  }

  &__menu {
    list-style: none;
    padding: 0;
  }

  &__item {
    margin-bottom: 20px;
    width: 100%;
  }

  &__link {
    border-radius: 5px;
    display: flex;
    align-items: center;
    padding: 10px;
    color: $color-font-link;
    text-decoration: none;
    position: relative;
    font-size: 14px;

    &::after {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      background-image: url("../img/svg/arrow.svg");
      right: 10px;
    }

    &:hover {
      color: $color-default-white;
      background-color: $color-paginaton-background-active;

      &::after {
        background-image: url("../img/svg/arrow-white.svg");
      }
    }
  }

  &__icon {
    display: inline-block;
    margin-right: 14px;
  }

  &__user {
    margin-top: auto;
  }

  &__manager,
  &__rating,
  &__experience {
    margin: 5px 0;
  }

  @include tablet {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding: 10px;

    &__title {
      font-size: 0;
      margin: 0;
    }

    &__menu {
      display: flex;
      flex-direction: row;
    }

    &__item {
      width: auto;
      margin: 0;
    }

    &__link {
      font-size: 0;

      &::after {
        display: none;
      }
    }

    &__user {
      margin: 0;
    }
  }

  @include mobile {
    position: relative;

    &__menu {
      display: none;

      &--active {
        background: $color-background;
        position: absolute;
        display: flex;
        width: 100%;
        top: 40px;
        left: 0;
        z-index: 10;
        flex-direction: column;
      }
    }

    &__link {
      padding-left: 30px;
      font-size: 17px;
    }
  }
}
